import React from "react";
import { graphql } from "gatsby";
import Projects from '../components/projectList'

class DesignProjects extends React.Component{

render (){
  return (
    <Projects data={this.props.data} path={this.props.uri}/>
  );
};
}

//sort: { fields: [frontmatter___date], order: DESC }
export const postsQuery = graphql`
  query($limit: Int!, $skip: Int!) {
    allMarkdownRemark(
      filter: { fileAbsolutePath: { regex: "//design//" } }
      limit: $limit
      skip: $skip
    ) {
      edges {
        node {
          id
          excerpt
          frontmatter {
            title
            shortTitle
            date
            path
            author
            excerpt
            tags
            coverImage {
              publicURL
            }
            coverVideo {
              publicURL
            }
            links
            readMoreButton
            order
          }
        }
      }
    }
  }
`;

export default DesignProjects;
